<template>
  <div class='detail'>
    <!-- <ul> <li v-for='item in $store.state.cartList'>{{item}}</li></ul>  -->
    <detailnavtop @titleclick='titleclick' ref='nav'></detailnavtop>
    <scroll class='scroll' ref='scroll' @scroll='contentScroll' :probeType='3'>
    <detailswiper :topImages='topImages'></detailswiper>
    <detailbaseinfo :goods='goods'></detailbaseinfo>
    <detailshopinfo :shop='shop'></detailshopinfo>
    <detailgoodsinfo :detailInfo='detailInfo' @imageLoad='imageLoad' ></detailgoodsinfo>
    <detailparaminfo :paramInfo='paramInfo' ref='param'></detailparaminfo>
    <detailcommont :commentInfo='commentInfo' ref='comment'></detailcommont>
    <detailbottom></detailbottom>
    </scroll>
    <backTop @click.native='backclick' v-show='isshow'></backTop>
    <detailbottom @addEvent="addToCart"></detailbottom>
 </div>
</template>

<script>
import {mapActions} from 'vuex'
import {getDetail,Goods,Shop,GoodsParam} from 'network/detail.js'
import scroll from 'components/common/scroll/scroll'
import detailnavtop from './children/detailnavtop'
import detailswiper from './children/detailswiper'
import detailgoodsinfo from './children/detailgoodsinfo'
import detailbaseinfo from './children/detailbaseinfo'
import detailshopinfo from './children/detailshopinfo'
import detailparaminfo from './children/detailparaminfo'
import detailcommont from './children/detailcommont'
import backTop from 'components/content/backTop'
import detailbottom from './children/detailbottom'
export default {
  name: 'detail',
  data() { 
    return {
      iid:null,
      topImages:[],
      goods:{},
      shop:{},
      detailInfo:{},
      paramInfo:{},
      commentInfo:{},
      isshow:false,
      themeTopy:[0,5000,7000,8000],
      currentIndex:0
    }
  },
  created(){
    this.iid = this.$route.params.iid
    getDetail(this.iid).then(res => {
        //1、获取顶部的轮播图数据
        // console.log(res);
        const data = res.data.result
        // console.log(data);
        this.topImages=res.data.result.itemInfo.topImages

              //获取商品信息
      this.goods = new Goods(data.itemInfo,data.columns,data.shopInfo.services)

              //创建店铺信息的对象
            this.shop = new Shop(data.shopInfo)

            //保存商品的详情图片信息
            this.detailInfo = data.detailInfo

            //获取商品的参数信息
            this.paramInfo = new GoodsParam(data.itemParams.info,data.itemParams.rule)

            //取出评论信息
            if(data.rate.cRate !== 0){
              this.commentInfo = data.rate.list[0]
            }
            //根据最新的数据，dom已经渲染完成了，但图片还没有加载完，所以有些商品不对，一般offsetTop值不对都是因为图片问题
            // this.$nextTick(() => {
            //         this.themeTopy[1] = this.$refs.param.$el.offsetTop
            //         this.themeTopy[2] = this.$refs.comment.$el.offsetTop
            //         console.log(this.themeTopy[1]);
            // })

      })
  },
  components:{
    detailnavtop,
    detailswiper,
    detailgoodsinfo,
    detailbaseinfo,
    scroll,
    detailshopinfo,
    detailparaminfo,
    detailcommont,
    backTop,
    detailbottom
  },
  methods:{
    ...mapActions(['addCart']),
      //监听详情页的图片加载完成
    imageLoad(){
      this.$refs.scroll.refresh()
      this.themeTopy[1] = this.$refs.param.$el.offsetTop
      this.themeTopy[2] = this.$refs.comment.$el.offsetTop
      this.themeTopy[3] = this.$refs.comment.$el.offsetTop
    },
    backclick(){
    this.$refs.scroll.scroll && this.$refs.scroll.scroll.scrollTo(0,0,1000)//还可以第三个参数，时间，在多少时间内回到顶部
    },
      contentScroll(position){
      //决定backtop是否显示
      this.isshow = position.y < -500
      let Y=-position.y
        let _lenth = this.themeTopy.length;
				for(let i=0; i<_lenth-1; i++){
					if(this.currentIndex !== i && (Y >= this.themeTopy[i] && Y < this.themeTopy[i+1])){
						this.currentIndex = i;
						this.$refs.nav.current = this.currentIndex;
					}
				}
    },
    addToCart(){
      //1、获取购物车所需要的信息
      const product = {}
      product.image = this.topImages[0];
      product.title = this.goods.title;
      product.desc = this.goods.desc;
      product.price = this.goods.realPrice;
      product.iid = this.iid;
      //2、将商品添加到购物车
      this.addCart(product).then(res => {//把Actions的方法映射过来了
        console.log(res);
      })
      // this.$store.dispatch('addCart',product).then(res => {
      //   console.log(res);
      // })
    },
    titleclick(index){
      this.$refs.scroll.scroll.scrollTo(0,-this.themeTopy[index],100)
    }

  },
 }
</script>

<style scoped>
.detail{
  position: relative;
  height:100vh;
  background-color: #fff;
  z-index:1;
}
.scroll{
    overflow: hidden;
    position: absolute;
    top: 44px;
    bottom: 49px;
    left: 0;
    right: 0;
}
</style>