<template>
  <div>
    <navtop>
      <div slot='left' @click='backclick'><img src="~assets/img/common/back.svg" alt="" class='back'></div>
      <div slot='center' class='title'>
        <span class='title-item' v-for='(item,index) in title' :class='{active:index===current}' @click='btnclick(index)'>{{item}}</span>
        </div>
    </navtop>
 </div>
</template>

<script>
import navtop from 'components/content/navTop'
export default {
  name: 'detailnavtop',
  data() { 
    return {
      title:['商品','参数','评论','推荐'],
      current:0
    }
  },
  components:{
    navtop
  },
  methods:{
    btnclick(index){
      this.current=index
      this.$emit('titleclick',index);
    },
    backclick(){
      this.$router.push('/home')
    }
  },
 }
</script>

<style scoped>
.title{
  display: flex;
  font-size: 13px;
}
.title-item{
  flex: 1;
}
.active{
  color:deeppink;
}
.back{
  margin-top:11px;
}
</style>